import React,{ Component,PropTypes} from 'react';

export default class TestReactCh1 extends Component{
    constructor(props){
        super(props);
        this.state={
            TestReactCh1:1,
        };
        this.textClick=this.textClick.bind(this);
    }
    textClick(){
        this.setState({
            TestReactCh1:11
        });
    }
    render(){
        console.log("TestReactCh1");
        return(
            <div>
                <span onClick={this.textClick}>TestReactCh1,{this.state.TestReactCh1}</span>
                
            </div>
        )
    }
}