import {TRIGGER_CLICK,INIT_RANDOM, GOING_JUMP, HAPPY_TOGETHER, GOING_MOVE, GOING_JUMP_DONE, GOING_START, SENDING_FAILURE, SENDING_SUCCESS, NOTIFY_LEFT_POS, CHANGE_STAGE} from './actionTypes.js';
import {utilTool} from '../utils/util.js';
import { going_start } from './actions.js';

var ShapesNameOption=["frogA","frogB","frogC","frogA","frogB","frogC","frogA","frogB","frogC"];
var FlowStaffNameOption=["branch1-1","branch1-2","branch1-3",
"branch2-1","branch2-2","branch2-3",
"branch3-1","branch3-2","branch3-3",
"branch4-1","branch4-2","branch4-3",
"branch5-1","branch5-2","branch5-3",];
var util=new utilTool();
export default (state={init:false,score:0,initCount:15,group:[]}, action)=>{
    let node=null;
    let id;
    let newstate={};
    let leftPos=0;
    switch (action.type) {
        case TRIGGER_CLICK:
        return {...state};
            break;
        case INIT_RANDOM:
        let nodes=[];
        for (let index = 0; index < state.initCount; index++) {
            let node={
                idx:index,
                flowStaffName:FlowStaffNameOption[util.randomByRange(0,FlowStaffNameOption.length)],
                shapeName:ShapesNameOption[util.randomByRange(0,ShapesNameOption.length)],
                state:'stand',
                startMoveDelay:util.randomByRange(2,15),
                jumpClass:'',
                zIndex:1,
                moveClass:'moveStartInit',
                lastJumpLeft:3000,
                success:'success',
                stage:1,
                currentLeft:1600,
            };
            nodes.push(node);
        }
        console.log("random");
        return {...state,init:true,group:nodes};
            break;
        case NOTIFY_LEFT_POS:
     
        let leftpos=action.payload;
        state.group.forEach(function(item,idx,arr){
            item.currentLeft=leftpos[idx];
        });
        Object.assign(newstate,state);
        return newstate;
        break;

        case GOING_START:
        id=action.payload;
        state.group.some(function(item){
            if(item.idx === id)
            {
                node=item;
                node.jumpClass="";
                node.zIndex=1;
                node.moveClass='moveStartInit';
                return true;
            }
            else
            return false;
        });
   
        Object.assign(newstate,state);
        return newstate;
        break;

        case GOING_JUMP:
        id=action.payload.id;
        leftPos=action.payload.leftPos;
        state.group.some(function(item){
            if(item.idx === id)
            {
                node=item;
                node.jumpClass="running";
                node.state='jumping';
                node.zIndex=10;
                node.lastJumpLeft=leftPos;
                return true;
            }
            else
            return false;
        });
   
        Object.assign(newstate,state);
        return newstate;
        break;

        case SENDING_FAILURE:
        id=action.payload;
        
        state.group.some(function(item){
            if(item.idx === id)
            {
                node=item;
                node.jumpClass="fail";
                node.state='fail';
                return true;
            }
            else
            return false;
        });
   
        Object.assign(newstate,state);
        return {...newstate,score:state.score};
        break;

        case CHANGE_STAGE:
        id=action.payload.id;
        let sg=action.payload.sg;
        state.group.some(function(item){
            if(item.idx === id)
            {
        
                node=item;
                node.stage=sg;
                return true;
            }
            else
            return false;
        });
   
        Object.assign(newstate,state);
        return newstate;
        break;


        case SENDING_SUCCESS:
        id=action.payload;
        
        state.group.some(function(item){
            if(item.idx === id)
            {
        
                node=item;
                node.jumpClass="recover";
                node.state='success';
                //node.lastJumpLeft=0;
                return true;
            }
            else
            return false;
        });
   
        Object.assign(newstate,state);
        return {...newstate,score:state.score+1};
        return newstate;
        break;

        case GOING_JUMP_DONE:
        id=action.payload;
        state.group.some(function(item){
            if(item.idx === id)
            {
                node=item;
                node.jumpClass="";
                node.zIndex=1;
                node.state='stand';
                return true;
            }
            else
            return false;
        });
        Object.assign(newstate,state);
        return newstate;
        break;

        case GOING_MOVE:
        id=action.payload;
        state.group.some(function(item){
            if(item.idx === id)
            {
                node=item;
                let bidx=node.flowStaffName.substr(6,1);
                node.moveClass="moveStartPoint-"+bidx+" branch-transform-"+bidx;
                node.flowStaffName+=" branch-transform-"+bidx;
               // node.shapeName+=("-"+bidx);
                return true;
            }
            else
            return false;
        });
        Object.assign(newstate,state);
        return newstate;
        break;



        case HAPPY_TOGETHER:
        state.group.map((item,idx,arr)=>{
            item.jumpClass="running";
        });
        let st={};
        Object.assign(st,state);
        return st;
        break;
        default:
        return {...state};
            break;
    }
}