import React,{ Component,PropTypes} from 'react';

export default class TestReactCh2 extends Component{
    constructor(props){
        super(props);
        this.state={
            TestReactCh2:2,
        }
        this.textClick=this.textClick.bind(this);
    }
    textClick(){
        this.setState({
            TestReactCh2:2
        });
    }

    render(){
        console.log("TestReactCh2");
        return(
            <div>
               <span onClick={this.textClick}>TestReactCh2,{this.state.TestReactCh2}</span>
            </div>
        )
    }
}