//singleton
export var utilTool=(
function(){
    var _singleIns=null;
    var _sortFun=(a,b)=>{
        return b.fromPos - a.fromPos;
    };
    function utilSingleton(){
        if(!(this instanceof utilSingleton))
        return null;
        if(_singleIns == null)
            _singleIns = this;

        //define some const variables here
        this.detectLength=200;
        this.detectAreaOffset={left:'70px',top:'50px'};
        this.stageBoundery=[];
        return _singleIns;
    }
    utilSingleton.prototype.randomByRange=function(min,max){
        return Math.floor(Math.random() * (max - min) + min);
    }
    utilSingleton.prototype.putInStageBoundary=function(el){
        if(!el.hasOwnProperty("stage") && !el.hasOwnProperty("fromPos"))
        {
            console.log("incorrect properties");
            return;
        }
        this.stageBoundery.push(el);
        this.stageBoundery.sort(_sortFun);

    }
    return utilSingleton;
}
)();
