import React,{ Component,PropTypes} from 'react';
import {connect} from 'react-redux';
import './material.scss';
import './river.scss';
import { sending_success_action, sending_failure_action } from '../reducers/actions';
import {utilTool} from '../utils/util.js';
var utilSingleton=new utilTool();
class Board extends Component{

    constructor(props){
        super(props);
      
    }
    componentDidMount(){
       
    }

    
    render(){
        const{score}=this.props;
        return(
            <div class="board">
            <span class="boardText">Score:{score}</span>
            </div>
            
        )
    }
}
var mapStatesToProps=function(state,ownProps){
    return {
        score:state.events.score
    }
};

export default connect(
    mapStatesToProps,
    null,
    null,
    { withRef: true }
  )(Board);