import React,{ Component,PropTypes} from 'react';
import TestReactCh1 from './TestReactCh1.js';
import TestReactCh2 from './TestReactCh2.js';

export default class TestReactParent extends Component{
    constructor(props){
        super(props);
        this.state={
            TestReactParent:3,
        };
        this.textClick=this.textClick.bind(this);
    }
    textClick(){
        this.setState({
            TestReactParent:33
        });
    }
    render(){
        console.log("TestReactParent");
        return(
            <div>
             
                <span onClick={this.textClick}>TestReactParent,{this.state.TestReactParent}</span>
                <TestReactCh1/>
                <TestReactCh2/>
            </div>
        )
    }
}