import React, { Component } from 'react';
import {connect} from 'react-redux';
import './material.scss';
import './river.scss';
import ShapeCmp from './ShapeCmp.js';
import HOC from "./VariationShapeCmp.js";
import {init_random_action,happy_together_action,going_jump_action,notify_left_pos_action} from '../reducers/actions';
import TestReactParent from './TestReactParent.js';
import DetectArea from './DetectArea.js';
import bgm from '../assets/bgm.wav';
import jumpSd from '../assets/jump.wav';
import failSd from "../assets/fail.wav";
import Board from "./Board.js";
class App extends Component {
  totalTime=0;
  constructor(props,context){
    super(props,context);
    this.resetClick=this.resetClick .bind(this);
    this.happyClick=this.happyClick .bind(this);
    this.childrenRefs=[];
    for(let i=0;i<15;i++){
      this.childrenRefs.push(React.createRef());
    }
    this.detectRefs=[];
    for(let i=0;i<3;i++){
      this.detectRefs.push(React.createRef());
    }
    this.jumpSdRef=React.createRef();
    this.failSdRef=React.createRef();
  }
  componentDidMount(){
  

    const{initCount}=this.props;
    this.props.dispatch(init_random_action(this.refs.canvasRef));

    let lefts=[];
    
    setInterval(()=>{
      this.totalTime+=0.3;
      let templeft=0;
      lefts.length=0;
      for(let i=0;i<initCount;i++){
        templeft=this.childrenRefs[i].current.getWrappedInstance().currentLeft();
        lefts.push(templeft);
      }
      this.props.dispatch(notify_left_pos_action(lefts));
      for(let i=0;i<3;i++){
        this.detectRefs[i].current.getWrappedInstance().checkDetect();
      }
    },300);
  }
  detectCheckTik(){
    
  }
  resetClick(e){
    this.props.dispatch(init_random_action(this.refs.canvasRef));
  }
  happyClick(e){
    const {group}=this.props;
    let p=Promise.resolve();
    for (const item of group) {
      p=p.then(
        (res)=>{
          return new Promise(function(resolver,reject){
            setTimeout(() => {
              resolver(item.idx);
            }, 1000);
          }).then(
            res=> this.props.dispatch(going_jump_action(res))
          )
        }
      );
    }
   // this.props.dispatch(happy_together_action());
  }
  playJumpSound=()=>{
    this.jumpSdRef.current.play();
  }
  playFailSound=()=>{
    this.failSdRef.current.play();
  }
  render() {
    const {group}=this.props;
   
    return (
      <div>
 
        <audio autoplay="autoplay" controls="controls" style={{visibility:'hidden'}}>    
         <source src={bgm}/>    
           </audio> 
        <audio src={jumpSd} controls ref={this.jumpSdRef} style={{visibility:'hidden'}}/>
        <audio src={failSd} controls ref={this.failSdRef} style={{visibility:'hidden'}}/>
        {/* <Board/> */}
        <div class="river">
          <div class="wave"></div>
          <div class="wave"></div>
          <div class="wave"></div>
          <div class="wave"></div>
          <div class="wave"></div>
          <div class="wave"></div>
       
       {
         
         group.map(
           (item,idx,arr)=>{
             let showNotice=false;
            
            // if(idx===0)showNotice=true;
            // if(showNotice === true){
            //   return <HOC shapeName={item.shapeName} idx={item.idx} ref={this.childrenRef[idx]}
            // />
            // }
            // else
            //  return <ShapeCmp shapeName={item.shapeName} idx={item.idx} ref={this.childrenRef[idx]}
            //  />
            return <ShapeCmp shapeName={item.shapeName} idx={item.idx} ref={this.childrenRefs[idx]} 
            playJump={()=>this.playJumpSound()}/>
           }
         )
       }

       
        <DetectArea detectType={1} ref={this.detectRefs[0]}  playFail={()=>this.playFailSound()}/>
        <DetectArea detectType={2} ref={this.detectRefs[1]}  playFail={()=>this.playFailSound()}/>
        <DetectArea detectType={3} ref={this.detectRefs[2]}  playFail={()=>this.playFailSound()}/>
        </div>
        
        <div class="decoration-1"></div>
        <div class="decoration-3"></div>
        {/* <TestReactParent/> */}
      <button className="resetButton" onClick={this.resetClick}>Reset</button>
      <button className="resetButton" onClick={this.happyClick}>Happy Together</button>
      
      </div>
    );
  }
}
var mapStateToProps=(state)=>{
    return{
      initCount:state.events.initCount,
      init:state.events.init,
      group:state.events.group,
    }
}
export default connect(mapStateToProps,null)(App);
