import React,{ Component,PropTypes} from 'react';
import {connect} from 'react-redux';
import './material.scss';
import './river.scss';
import { sending_success_action, sending_failure_action } from '../reducers/actions';
import {utilTool} from '../utils/util.js';
var utilSingleton=new utilTool();
class DetectArea extends Component{
    fromPos=0;
    toPos=0;
    detectBoundary=0;
    stageIdx=0;
    isStarted=false;
    whichStyle='';
    constructor(props){
        super(props);
        this.checkDetect=this.checkDetect.bind(this);
        this.detectAreaRef=React.createRef();
    }
    componentDidMount(){
        let rect = this.detectAreaRef.current.getBoundingClientRect();
        this.fromPos=rect.left;
        this.detectBoundary=this.fromPos-450;
        this.toPos=this.fromPos+utilSingleton.detectLength;
        utilSingleton.putInStageBoundary({stage:this.stageIdx, fromPos:this.fromPos});
    }
    getStyle=()=>{
        const{detectType}=this.props;
        this.stageIdx = detectType;
        switch(detectType){
            case 1:
            this.whichStyle = "10%";
            break;
            case 2:
            this.whichStyle = "40%";
            break;
            case 3:
            this.whichStyle = "70%";
            break;
        }
        return{
            left:this.whichStyle
        }
    }
    checkDetect(){
        const{groupPos}=this.props;
        groupPos.forEach((element)=>{
            if( element.state !== 'jumping'){
                
                if(element.currentLeft <= this.fromPos){
                    if(element.lastPos <= this.toPos && element.lastPos >= this.fromPos)
                    {
                        this.props.dispatch(sending_success_action(element.idx));
                    }
                    else
                    {
                        this.props.playFail();
                        this.props.dispatch(sending_failure_action(element.idx));
                    }
                }
            }
            else if( element.state === 'stand')
            {
                
            }
        });
    }
    render(){
        const{detectType}=this.props;
        return(
            <div class="bridge" style={this.getStyle()}>
            <div class="detectArea" style={(()=>{return {left:utilSingleton.detectAreaOffset.left, top:utilSingleton.detectAreaOffset.top}})()} ref={this.detectAreaRef}></div>
            </div>
            
        )
    }
}
var mapStatesToProps=function(state,ownProps){
    let nodes = state.events.group;
    let node = null;
    let groupPos = [];
    
    nodes.forEach(element => {
        groupPos.push({idx:element.idx, lastPos:element.lastJumpLeft,
            stage:element.stage,currentLeft:element.currentLeft,state:element.state});
    });
    return {
        groupPos:groupPos
    }
};

export default connect(
    mapStatesToProps,
    null,
    null,
    { withRef: true }
  )(DetectArea);