import {TRIGGER_CLICK,EMIT_ROTATE,INIT_RANDOM,GOING_JUMP, HAPPY_TOGETHER, GOING_MOVE, GOING_JUMP_DONE, SENDING_FAILURE, SENDING_SUCCESS, NOTIFY_LEFT_POS, CHANGE_STAGE} from './actionTypes';

export const triangle_trigger_click_action=(data)=>{
    return {
        type:TRIGGER_CLICK,
        payload:data
    }
}
export const emit_rotate_action=(data)=>{
    return {
        type:EMIT_ROTATE,
        payload:data
    }
}
export const init_random_action=(data)=>{
    return {
        type:INIT_RANDOM,
        payload:data
    }
}
export const going_jump_action=(data)=>{
    return{
        type:GOING_JUMP,
        payload:data
    }
}
export const going_jump_ani=(data)=>{
    return (dispatch)=>{
        dispatch(going_jump_action(data));
        
    }
}
export const going_start=(data)=>{
    return{
        type:going_start,
        payload:data
    }
}
export const finish_jump_action=(data)=>{
    return{
        type:GOING_JUMP_DONE,
        payload:data
    }
}
export const going_move_action=(data)=>{
    return{
        type:GOING_MOVE,
        payload:data
    }
}
export const happy_together_action=()=>{
    return {
        type:HAPPY_TOGETHER,
    }
}
export const notify_left_pos_action=(data)=>{
    return{
        type:NOTIFY_LEFT_POS,
        payload:data
    }
}
export const sending_failure_action=(data)=>{
    return{
        type:SENDING_FAILURE,
        payload:data
    }
}
export const sending_success_action=(data)=>{
    return{
        type:SENDING_SUCCESS,
        payload:data
    }
}
export const change_stage_action=(data)=>{
    return{
        type:CHANGE_STAGE,
        payload:data
    }
}