import React, { Component, PropTypes, PureComponent,Fragment } from "react";
import { connect } from "react-redux";
import "./material.scss";
import "./river.scss";
import TestPureCmp from "./TestPureCmp.js";
import {
  triangle_trigger_click_action,
  going_jump_action,
  going_move_action,
  going_jump_ani,
  finish_jump_action,
  going_start,
  notify_left_pos_action,
  sending_success_action,
  change_stage_action
} from "../reducers/actions";
import { utilTool } from "../utils/util.js";
var util = new utilTool();
class ShapeCmp extends Component {
  //itemV={name:'yu',age:35};
  whichFloor=-1;
  speed=-1;
  _currentLeft=-1;
  isJumping=false;
  constructor(props, context) {
    super(props, context);
    this.onShapeClick = this.onShapeClick.bind(this);
    this.aniCallfunction = this.aniCallfunction.bind(this);
    this.readyToMove = this.readyToMove.bind(this);
    this.currentLeft = this.currentLeft.bind(this);
    this.flowRef = React.createRef();
    this.turtleRef = React.createRef();
  }
  componentDidMount() {
    const { flowStaffName } = this.props;
    this.readyToMove();
    this.whichFloor=flowStaffName.substr(6,1);
    this.speed=2000/(20-2*(this.whichFloor - 1));
  }

  readyToMove(){
    const { onGoingMove, idx, startMoveDelay, flowStaffName } = this.props;
    setTimeout(() => {
      onGoingMove(idx);
    }, startMoveDelay * 1000);
  }
  currentLeft(){
    let rect = this.turtleRef.current.getBoundingClientRect();
     //return (1600 - this.speed * totalTime) ;
     const{state,stage,currentLeft}=this.props;
    //  let sg=1;
    //  util.stageBoundery.some((el)=>{
    //     if(rect.left > el.fromPos)
    //     {
    //       sg=el.stage;
    //       return true;
    //     }
    //  });
    //  if(stage !== sg)
    //  {
    //    this.props.onChangeStage({id:this.props.idx,sg:sg});
    //  }
    if(currentLeft < 0)
    this.props.onSendingSuccess(this.props.idx);
     return rect.left;
  }

  setStyle() {
    const { zIndex } = this.props;
    return {
     // left: util.randomByRange(1500, 1800),
      zIndex: zIndex
    };
  }
  // shouldComponentUpdate(nextprops,nextstate){
  //     if(nextprops.jumpClass !== this.props.jumpClass ||
  //         (this.props.left !== nextprops.left && this.props.top != nextprops.top) )
  //     return true;
  //     else
  //     return false;
  // }

  onShapeClick(event) {
    if(this.props.state === 'fail')return;
    let rect = this.turtleRef.current.getBoundingClientRect();
    this.props.onGoingJump({id:this.props.idx,leftPos:rect.left});
    this.props.playJump();
    this.isJumping=true;
//console.log("jump left:",rect.left);
    this.turtleRef.current.addEventListener("webkitAnimationEnd", this.aniCallfunction,false);
    this.turtleRef.current.addEventListener("animationend", this.aniCallfunction,false);
    this.turtleRef.current.addEventListener("oanimationend", this.aniCallfunction,false);
  }
  aniCallfunction=()=>{
   this.isJumping=false;
    this.turtleRef.current.removeEventListener("webkitAnimationEnd", this.aniCallfunction,false);
    this.turtleRef.current.removeEventListener("animationend", this.aniCallfunction,false);
    this.turtleRef.current.removeEventListener("oanimationend", this.aniCallfunction,false);
    this.props.onFinishJump(this.props.idx);
};
  render() {
    const {
      onTriClick,
      shapeName,
      showNotice,
      jumpClass,
      flowStaffName,
      moveClass
    } = this.props;
    let styleObj = this.setStyle();
    let finalClass = shapeName + " " + jumpClass;
   
    return (
      <Fragment>
        <div class={flowStaffName} ref={this.flowRef}>
         
        </div>
        <span class={moveClass}  style={styleObj}>
        <li
            className={finalClass}
            onClick={this.onShapeClick}
            ref={this.turtleRef}
           
          >
            {this.props.children}
          </li>
          </span>
        {/* <TestPureCmp item={this.state.item}/> */}
      </Fragment>
    );
  }
}

const mapStateToProps = (state, ownprops) => {
  let nodes = state.events.group;
  let node = null;
  let resultProps = null;
  nodes.every((element, idx, arr) => {
    if (element.idx === ownprops.idx) {
      node = element;
      return false;
    } else return true;
  });
  return {
    idx:node.idx,
    stage:node.stage,
    shapeName: node.shapeName,
    flowStaffName: node.flowStaffName,
    jumpClass: node.jumpClass,
    startMoveDelay: node.startMoveDelay,
    zIndex:node.zIndex,
    moveClass:node.moveClass,
    state:node.state,
    currentLeft:node.currentLeft
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onTriClick: () => {
      dispatch(triangle_trigger_click_action());
    },
    onGoingJump: (id) => {
      dispatch(going_jump_ani(id));
    },
    onGoingMove: id => {
      dispatch(going_move_action(id));
    },
    onFinishJump: id=>{
        dispatch(finish_jump_action(id));
    },
    onGoingStart: id=>{
      dispatch(going_start(id));
    },
    onSendingSuccess:id=>{
      dispatch(sending_success_action(id));
    },
    onChangeStage:data=>{
      dispatch(change_stage_action(data));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,null, { withRef: true }
)(ShapeCmp);
